<template>
  <div>
    <el-dialog
      title="计划详情"
      :visible.sync="localDialog"
      @close="closeDialog"
    >
      <!-- 表单内容 -->
      <el-form :model="taskInfo" :rules="rules" ref="ruleForm">
        <el-form-item label="计划名" label-width="80px" prop="title">
          <el-input
            v-model="taskInfo.title"
            auto-complete="off"
            placeholder="请输入计划名"
            maxlength="30"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="计划内容" label-width="80px">
          <el-input
            v-model="taskInfo.content"
            type="textarea"
            maxlength="300"
            placeholder="请输入计划内容"
            rows="5"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="计划时间" label-width="80px">
          <el-time-select
            v-model="taskInfo.task_time"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }"
            placeholder="选择时间"
          >
          </el-time-select>
        </el-form-item>
        <el-form-item label="计划循环" label-width="80px">
          <el-button-group>
            <el-button
              v-for="(item, index) in [
                '一',
                '二',
                '三',
                '四',
                '五',
                '六',
                '日'
              ]"
              :key="index"
              :type="cycle_trans['index' + (index + 1)] ? 'primary' : ''"
              :icon="cycle_trans['index' + (index + 1)] ? 'el-icon-check' : ''"
              @click="toggle_cycle(index + 1)"
            >
              星期{{ item }}
            </el-button>
          </el-button-group>
        </el-form-item>
      </el-form>

      <!-- 拟态框按钮 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="onSubmit('ruleForm')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    dialog: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    taskInfo: {
      type: Object,
      default: function() {
        return {
          title: "",
          content: "",
          cycle: "0"
        };
      }
    }
  },
  data() {
    return {
      localDialog: this.dialog,
      localTaskInfo: this.taskInfo,
      localCycle: this.taskInfo.cycle.split("#"),
      rules: {
        title: [{ required: true, message: "请输入计划名称", trigger: "blur" }]
      }
    };
  },
  methods: {
    closeDialog() {
      this.localDialog = false;
      this.$emit("dialogchange", this.localDialog);
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.localTaskInfo.cycle = this.localCycle.join("#");
          this.closeDialog();
          this.$emit("taskinfochange", this.localTaskInfo);
        } else {
          return false;
        }
      });
    },
    toggle_cycle(index) {
      let cycle_index = this.localCycle.indexOf(index + "");
      if (cycle_index !== -1) {
        this.localCycle.splice(cycle_index, 1);
      } else {
        if (this.localCycle.indexOf("0") !== -1) {
          this.localCycle = [];
        }
        this.localCycle.push(index + "");
        this.localCycle.sort();
      }
    }
  },
  watch: {
    dialog(val) {
      this.localDialog = val;
    },
    taskInfo(val) {
      this.localTaskInfo = val;
      this.localCycle = val.cycle.split("#");
    }
  },
  computed: {
    cycle_trans() {
      let cycle = {};
      // 当循环只有一个数的时候
      if (this.localCycle.length === 1) {
        // 当只有一天循环的时候
        for (let i = 1; i < 8; i++) {
          cycle["index" + i] = i + "" === this.localCycle[0];
        }
      } else {
        // 当有多天循环的时候
        for (let i = 1; i < 8; i++) {
          cycle["index" + i] = this.localCycle.indexOf(i + "") !== -1;
        }
      }

      return cycle;
    }
  }
};
</script>
